* {
  box-sizing: border-box;
}

body {
  background: #000;
  color: #fff;
  height: 100vh;
  width: 100vw;
  margin: 0;
  padding: 0;
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
  line-height: 1.5;
  overflow: hidden;
}
body#bg-img {
  background: url(../img/background.jpg);
  background-attachment: fixed;
  background-size: cover;
}
body:after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  z-index: -1;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.85);
}

h1,
h2,
h3 {
  margin: 0;
  font-weight: 400;
}
h1.lg-heading,
h2.lg-heading,
h3.lg-heading {
  font-size: 3vw;
}
h1.sm-heading,
h2.sm-heading,
h3.sm-heading {
  margin-bottom: 2rem;
  font-size: 1.5vw;
}

a {
  color: #fff;
  text-decoration: none;
}

#root,
#warpper {
  height: 100vh;
}

header {
  position: fixed;
  z-index: 4;
  width: 100%;
}

.text-secondary {
  color: #ff6600;
}

.menu-btn {
  position: absolute;
  z-index: 5;
  right: 35px;
  top: 35px;
  cursor: pointer;
  transition: all 0.5s ease-out;
}
.menu-btn .btn-line {
  width: 28px;
  height: 3px;
  margin: 0 0 5px 0;
  background: #fff;
  transition: all 0.5s ease-out;
}
.menu-btn.close {
  transform: rotate(180deg);
}
.menu-btn.close .btn-line:nth-child(1) {
  transform: rotate(45deg) translate(5px, 5px);
}
.menu-btn.close .btn-line:nth-child(2) {
  opacity: 0;
}
.menu-btn.close .btn-line:nth-child(3) {
  transform: rotate(-45deg) translate(7px, -6px);
}

.menu {
  position: fixed;
  top: 0;
  width: 100%;
  opacity: 0.95;
  visibility: hidden;
}
.menu.show {
  visibility: visible;
}
.menu-branding,
.menu-nav {
  display: flex;
  flex-flow: column wrap;
  align-items: center;
  justify-content: center;
  float: left;
  width: 50%;
  height: 100vh;
  overflow: hidden;
}
.menu-branding.show,
.menu-nav.show {
  transform: translate3d(0, 0, 0);
}
.menu-branding {
  background: #000;
  transform: translate3d(0, -100%, 0);
  transition: all 0.5s ease-out;
}
.menu-nav {
  margin: 0;
  padding: 0;
  background: black;
  list-style: none;
  transform: translate3d(0, 100%, 0);
  transition: all 0.5s ease-out;
}
.menu .portrait {
  width: 250px;
  height: 250px;
  background: url("../img/portrait.jpg");
  border-radius: 50%;
  border: solid 3px #ff6600;
}
.menu .nav-item {
  transform: translate3d(600px, 0, 0);
  transition: all 0.5s ease-out;
}
.menu .nav-item.show {
  transform: translate3d(0, 0, 0);
}
.menu .nav-item.current > a {
  color: #ff6600;
}
.menu .nav-link {
  display: inline-block;
  position: relative;
  font-size: 30px;
  text-transform: uppercase;
  padding: 1rem 0;
  font-weight: 500;
  color: #fff;
  text-decoration: none;
  transition: all 0.5s ease-out;
}
.menu .nav-link:hover {
  color: #ff6600;
}

.nav-item:nth-child(1) {
  transition-delay: 0.1s;
}

.nav-item:nth-child(2) {
  transition-delay: 0.2s;
}

.nav-item:nth-child(3) {
  transition-delay: 0.3s;
}

.nav-item:nth-child(4) {
  transition-delay: 0.4s;
}

main {
  height: 100%;
}
main .icons {
  margin-top: 1rem;
}
main .icons a {
  padding: 0.4rem;
  font-size: 1.3vw;
}
main .icons a:hover {
  color: #ff6600;
  transition: all 0.5s ease-out;
}
main#home {
  overflow: hidden;
  padding: 2vw;
}
main#home .lg-heading {
  font-size: 8vw;
  margin-top: 20vh;
}
main#home .sm-heading {
  font-size: 2vw;
  padding: 1vh 2vw;
  background: rgba(5, 5, 5, 0.5);
}
main#about {
  overflow: hidden;
}
main#about .title {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 3;
  padding: 2vh 2vw;
}

.about-info {
  overflow: hidden;
  width: 100%;
  height: 100%;
  font-size: 1.5vw;
}
.about-info #slider {
  position: relative;
  width: 400%;
  height: 100%;
  margin: 0px;
  left: 0;
  transition: all 0.8s;
}
.about-info #slider .sld {
  width: 25%;
  height: 100%;
  float: left;
  position: relative;
  background-image: url(../img/background.jpg);
  opacity: 0;
  transition: all 0.2s;
}
.about-info #slider .sld::after {
  content: "";
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  background: rgba(0, 0, 0, 0.8);
  z-index: 0;
}
.about-info #slider .sld .txtClip {
  width: 40%;
  height: 100%;
  font-size: 50vw;
  font-family: fantasy;
  line-height: 100vh;
  background-image: url(../img/background.jpg);
  background-position: 0px 0px;
  background-size: cover;
  background-attachment: fixed;
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 1;
  text-align: center;
  transition: left 0.8s;
}
.about-info #slider .sld .txtClip.left {
  left: 60%;
}
.about-info #slider .sld .line {
  height: 80%;
  width: 2px;
  position: fixed;
  top: 50%;
  left: 40%;
  transform: translate(0%, -50%);
  background: linear-gradient(#111 0%, #fff 90%);
  z-index: 1;
  transition: all 0.8s;
}
.about-info #slider .sld .line.left {
  top: 50%;
  left: 60%;
  transform: translate(0%, -50%);
}
.about-info #slider .sld .data {
  width: 45%;
  font-family: arial;
  color: #fff;
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-5%, -50%);
  z-index: 1;
  opacity: 0;
  transition: all 0.8s;
}
.about-info #slider .sld .data .location {
  padding-bottom: 3px;
  border-bottom: 2px solid #fff;
}
.about-info #slider .sld .data .title-info {
  font-size: 3vw;
  font-family: fantasy;
}
.about-info #slider .sld .data .title-info span {
  display: block;
}
.about-info #slider .sld .data .title-info span.tit2 {
  font-size: 2vw;
}
.about-info #slider .sld .data .info {
  width: 100%;
  margin-top: 5px;
  font-family: arial;
  text-align: justify;
}
.about-info #slider .sld .data .info .hastag {
  font-size: 1vw;
  opacity: 0.5;
}
.about-info #slider .sld .data.left {
  top: 50%;
  left: 5%;
  transform: translate(0%, -50%);
  text-align: right;
}
.about-info #slider .sld.active {
  opacity: 1;
}
.about-info #slider .sld.active .data {
  opacity: 1;
}
.about-info #navigation {
  position: absolute;
  bottom: 50px;
  left: 50%;
  transform: translateX(-50%);
  z-index: 2;
}
.about-info #navigation span {
  width: 2vw;
  height: 2vw;
  margin: 0 0.3vw;
  display: inline-block;
  background-color: rgba(255, 255, 255, 0.5);
  border-radius: 50%;
  outline: none;
  border: 2px solid rgba(255, 255, 255, 0.5);
  transition: all 0.5s;
}
.about-info #navigation span:hover {
  border: 5px solid rgba(255, 102, 0, 0.8);
}
.about-info #navigation span.active {
  background-color: #ff6600;
  border: 5px solid rgba(0, 0, 0, 0.5);
}

#main-footer {
  text-align: center;
  padding: 1rem;
  background: black;
  color: #fff;
  height: 60px;
}

@media screen and (max-width: 768px) {
  main {
    align-items: center;
    text-align: center;
  }
  main .lg-heading {
    line-height: 1;
    margin-bottom: 1rem;
    font-size: 12vw;
  }
  main .sm-heading {
    font-size: 3.5vw;
  }
  main .icons a {
    padding: 1.5vw;
    font-size: 3vw;
  }
  main#home {
    padding: 4vw;
  }
  main#home .lg-heading {
    font-size: 12vw;
    margin-top: 30vh;
  }
  main#home .sm-heading {
    font-size: 4vw;
  }
  main#about .title {
    text-align: left;
  }
  main#about .title .lg-heading {
    font-size: 4vw;
  }
  main#about .title .sm-heading {
    font-size: 2vw;
  }
  main#about .about-info {
    font-size: 3vw;
  }
  main#about .about-info #slider .sld .txtClip {
    width: 100%;
    height: 50vw;
    line-height: 50vw;
  }
  main#about .about-info #slider .sld .txtClip.left {
    left: 0;
  }
  main#about .about-info #slider .sld .line {
    height: 2px;
    width: 80%;
    top: 50vw;
    left: 10%;
    transform: translate(0, 0);
    background: linear-gradient(to left, #111 0%, #fff 90%);
  }
  main#about .about-info #slider .sld .line.left {
    top: 50vw;
    left: 10%;
    transform: translate(0, 0);
    background: linear-gradient(to right, #111 0%, #fff 90%);
  }
  main#about .about-info #slider .sld .data {
    width: 80%;
    top: 55vw;
    left: 10%;
    transform: translate(0%, 0%);
    text-align: left;
  }
  main#about .about-info #slider .sld .data .title-info {
    font-size: 6vw;
  }
  main#about .about-info #slider .sld .data.left {
    top: 55vw;
    left: 10%;
    transform: translate(0%, 0%);
    text-align: right;
  }
  main#about .about-info #navigation a {
    width: 4vw;
    height: 4vw;
  }
  ul.menu-nav,
  div.menu-branding {
    float: none;
    width: 100%;
    min-height: 0;
  }
  ul.menu-nav.show,
  div.menu-branding.show {
    transform: translate3d(0, 0, 0);
  }
  .menu-nav {
    height: 75vh;
    transform: translate3d(-100%, 0, 0);
    font-size: 24px;
  }
  .menu-branding {
    height: 25vh;
    transform: translate3d(100%, 0, 0);
  }
  .menu-branding .portrait {
    width: 150px;
    height: 150px;
    background: url("../img/portrait_small.jpg");
  }
}

@media screen and (max-width: 500px) {
  main#home h1 {
    margin-top: 10vh;
  }
}
